'use client';

import { useCollection, useMemoFirebase } from '@/firebase';
import { collection } from 'firebase/firestore';
import { firestore } from '@/lib/firebase';
import Header from '@/components/layout/header';
import Footer from '@/components/layout/footer';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Card, CardContent, CardHeader } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Skeleton } from '@/components/ui/skeleton';
import { Users, BookOpen, Target } from 'lucide-react';
import Link from 'next/link';
import { Button } from '@/components/ui/button';

interface UserProfile {
  id: string;
  name: string;
  photoURL?: string;
  bio?: string;
  skillsToTeach: { name: string; verified: boolean }[];
  skillsToLearn: { name: string }[];
}

export default function CommunityPage() {
  const usersCollectionRef = useMemoFirebase(() => collection(firestore, 'users'), []);
  const { data: users, isLoading, error } = useCollection<UserProfile>(usersCollectionRef);

  const getInitials = (name: string | null | undefined) => {
    if (!name) return '';
    return name.split(' ').map((n) => n[0]).join('');
  };

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <main className="flex-1 py-20 md:py-28">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center max-w-3xl mx-auto">
            <h1 className="text-4xl font-bold tracking-tight text-foreground sm:text-5xl font-headline">
              Meet the Community
            </h1>
            <p className="mt-4 text-xl leading-8 text-muted-foreground">
              Browse all the talented members of the SkillSwap network.
            </p>
          </div>

          <div className="mt-16">
            {isLoading ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                {Array.from({ length: 6 }).map((_, i) => (
                  <Card key={i} className="bg-card/70 border-border/50">
                    <CardHeader className="flex flex-row items-center gap-4">
                      <Skeleton className="h-14 w-14 rounded-full" />
                      <div className="space-y-2">
                        <Skeleton className="h-6 w-32" />
                      </div>
                    </CardHeader>
                    <CardContent className="space-y-4">
                       <div>
                         <Skeleton className="h-5 w-24 mb-2" />
                         <div className="flex flex-wrap gap-2">
                            <Skeleton className="h-6 w-16" />
                            <Skeleton className="h-6 w-20" />
                         </div>
                       </div>
                       <div>
                         <Skeleton className="h-5 w-24 mb-2" />
                         <div className="flex flex-wrap gap-2">
                            <Skeleton className="h-6 w-24" />
                         </div>
                       </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            ) : error ? (
              <div className="text-center py-16 text-red-400">
                <Users className="mx-auto h-12 w-12" />
                <h3 className="mt-4 text-xl font-semibold">Error Loading Community</h3>
                <p className="mt-2 text-red-400/80">There was a problem fetching the user list. It might be a permission issue.</p>
              </div>
            ) : users && users.length > 0 ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                {users.map((user) => (
                  <Card key={user.id} className="bg-card/70 border-border/50 hover:border-primary/50 transition-all duration-300 hover:shadow-xl hover:shadow-primary/10 transform hover:-translate-y-1">
                    <CardHeader className="flex flex-col items-center text-center p-6">
                      <Avatar className="w-20 h-20 text-3xl ring-2 ring-primary/20 mb-4">
                        <AvatarImage src={user.photoURL || ''} alt={user.name || ''} />
                        <AvatarFallback>{getInitials(user.name)}</AvatarFallback>
                      </Avatar>
                      <h3 className="text-xl font-bold font-headline">{user.name}</h3>
                      <p className="text-sm text-muted-foreground mt-1 line-clamp-2">{user.bio || 'No bio yet.'}</p>
                    </CardHeader>
                    <CardContent className="px-6 pb-6 space-y-4">
                      <div>
                        <h4 className="font-semibold text-muted-foreground mb-2 flex items-center gap-2 text-sm"><BookOpen size={16} className="text-accent"/> Teaches</h4>
                        <div className="flex flex-wrap gap-2">
                          {user.skillsToTeach && user.skillsToTeach.length > 0 ? user.skillsToTeach.map(skill => (
                            <Badge key={skill.name} variant="default" className="text-sm bg-accent hover:bg-accent/80">{skill.name}</Badge>
                          )) : <p className="text-xs text-muted-foreground">No skills to teach yet.</p>}
                        </div>
                      </div>
                      <div>
                        <h4 className="font-semibold text-muted-foreground mb-2 flex items-center gap-2 text-sm"><Target size={16} className="text-secondary"/> Learns</h4>
                        <div className="flex flex-wrap gap-2">
                         {user.skillsToLearn && user.skillsToLearn.length > 0 ? user.skillsToLearn.map(skill => (
                            <Badge key={skill.name} variant="secondary" className="text-sm">{skill.name}</Badge>
                          )) : <p className="text-xs text-muted-foreground">No skills to learn yet.</p>}
                        </div>
                      </div>
                       <Button variant="outline" className="w-full mt-4">View Profile</Button>
                    </CardContent>
                  </Card>
                ))}
              </div>
            ) : (
              <div className="text-center py-16">
                <Users className="mx-auto h-12 w-12 text-muted-foreground" />
                <h3 className="mt-4 text-xl font-semibold text-foreground">Community is Empty</h3>
                <p className="mt-2 text-muted-foreground">Looks like you're the first one here! Check back later as more users join.</p>
              </div>
            )}
          </div>
        </div>
      </main>
      <Footer />
    </div>
  );
}
