
'use client';

import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Share2, User, LogOut, LayoutDashboard, Users } from 'lucide-react';
import { useUser } from '@/firebase';
import { signOut } from '@/lib/auth';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { useRouter, usePathname } from 'next/navigation';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { cn } from '@/lib/utils';


export default function Header() {
  const { user, isUserLoading } = useUser();
  const router = useRouter();
  const pathname = usePathname();

  const getInitials = (name: string | null | undefined) => {
    if (!name) return '';
    const names = name.split(' ');
    return names
      .map((n) => n[0])
      .slice(0, 2)
      .join('');
  };

  const navLinks = [
    { href: "/jobs", label: "Find a Swap" },
    { href: "/community", label: "Community" },
    { href: "/profile", label: "Profile" }
  ];

  return (
    <header className="fixed top-0 left-0 right-0 z-50">
      <div className="container mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16">
          <div className="flex items-center gap-8">
            <Link href="/" className="group flex items-center gap-2 text-xl font-headline font-bold">
              <Share2 className="w-7 h-7 text-primary transition-all duration-300 group-hover:text-accent group-hover:scale-110 group-hover:drop-shadow-[0_0_8px_hsl(var(--accent))]" />
              <h1 className="bg-gradient-to-r from-primary to-accent bg-clip-text text-transparent transition-all duration-300 group-hover:drop-shadow-[0_0_8px_hsl(var(--primary))]">
                SkillSwap
              </h1>
            </Link>
            {user && (
              <nav className="hidden md:flex items-center gap-2 bg-card/50 px-3 py-1.5 rounded-full border border-border/50">
                {navLinks.map(link => (
                   <Link key={link.href} href={link.href} passHref>
                    <Button variant="ghost" size="sm" className={cn("rounded-full transition-all", 
                      pathname === link.href ? "bg-primary text-primary-foreground hover:bg-primary/90 hover:text-primary-foreground" : "hover:bg-muted"
                    )}>
                      {link.label}
                    </Button>
                  </Link>
                ))}
              </nav>
            )}
          </div>
          <div className="flex items-center gap-4">
            {isUserLoading ? (
              <div className="w-10 h-10 animate-pulse bg-muted rounded-full" />
            ) : (
              <>
                {user ? (
                   <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                       <Avatar className="cursor-pointer ring-2 ring-transparent hover:ring-primary transition-all">
                        <AvatarImage src={user.photoURL || undefined} alt={user.displayName || ''} />
                        <AvatarFallback>{getInitials(user.displayName)}</AvatarFallback>
                      </Avatar>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent className="w-56" align="end">
                      <DropdownMenuLabel>My Account</DropdownMenuLabel>
                      <DropdownMenuSeparator />
                      <DropdownMenuItem onClick={() => router.push('/profile')}>
                        <User className="mr-2 h-4 w-4" />
                        <span>Profile</span>
                      </DropdownMenuItem>
                      <DropdownMenuItem onClick={() => router.push('/community')}>
                        <Users className="mr-2 h-4 w-4" />
                        <span>Community</span>
                      </DropdownMenuItem>
                      <DropdownMenuItem onClick={() => router.push('/jobs')}>
                        <LayoutDashboard className="mr-2 h-4 w-4" />
                        <span>Find a Swap</span>
                      </DropdownMenuItem>
                      <DropdownMenuSeparator />
                      <DropdownMenuItem onClick={signOut}>
                        <LogOut className="mr-2 h-4 w-4" />
                        <span>Log out</span>
                      </DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                ) : (
                  <Button 
                    onClick={() => router.push('/login')}
                    className="bg-primary text-primary-foreground hover:bg-primary/90 shadow-[0_0_15px_hsl(var(--primary)/0.5)] hover:shadow-[0_0_25px_hsl(var(--primary)/0.7)] transition-shadow duration-300"
                  >
                    Sign In
                  </Button>
                )}
              </>
            )}
          </div>
        </div>
      </div>
    </header>
  );
}
